@import "variable";
nav {
    ul {
        font-size      : 36px;
        align-items    : center;
        justify-content: space-between;

        li {
            list-style    : none;
            padding-bottom: 10px;
            text-transform: uppercase;

            a {
                text-decoration: none;
                color          : @primary-color;
            }
        }
    }
}